@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }
  
  body {
    @apply bg-dark-50 text-dark-900 font-sans;
  }
}

@layer components {
  .gradient-text {
    @apply bg-gradient-to-r from-primary-600 to-primary-400 bg-clip-text text-transparent;
  }
  
  .card {
    @apply bg-white rounded-xl shadow-lg border border-dark-200 p-6 hover:shadow-xl transition-all duration-300;
  }
  
  .btn-primary {
    @apply bg-primary-600 hover:bg-primary-700 text-white px-6 py-3 rounded-lg font-medium transition-colors duration-200;
  }
  
  .btn-secondary {
    @apply bg-dark-100 hover:bg-dark-200 text-dark-700 px-6 py-3 rounded-lg font-medium transition-colors duration-200;
  }
  
  .section-padding {
    @apply py-20 px-4 sm:px-6 lg:px-8;
  }
  
  .container-max {
    @apply max-w-7xl mx-auto;
  }
}

@layer utilities {
  .text-balance {
    text-wrap: balance;
  }
} 